import React,{ Component } from "react";
import { Container, Grid, Card, CardMedia, CardContent, Typography, CardActions, Button, withStyles } from "@material-ui/core";

import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import HeroImageComponent from "../../component/hero-image-component/HeroImageComponent";
import { HeroImage, TeamMemberInfo } from "../../data/BusinessDetails";
import TeamMemberCard from "./TeamMemberCard";

import CompanyDetailStore from "../../stores/BusinessDetailsStore";

import { inject, observer } from "mobx-react";

const styles = (theme: Theme) => ({
  
  cardGrid: {
    paddingTop: theme.spacing(5),
    paddingBottom: theme.spacing(8),
  },
  
  cardMedia: {
    paddingTop: '100%', // 16:9
  }
});
  

interface TeamPageProps {
  companyDetailStore?: CompanyDetailStore,
  classes?:any
}

@inject('companyDetailStore')
@observer
class TeamPage extends Component<TeamPageProps>{
    render(){
        const { classes } = this.props;
        return(<div>

<HeroImageComponent {...this.props.companyDetailStore!.businessDetails.teamInfo!.heroImage}></HeroImageComponent>

<Container className={classes.cardGrid} maxWidth="md">
          
          <Grid container spacing={4} justify="center" alignItems="stretch" >

          {this.props.companyDetailStore!.businessDetails.teamInfo!.teamMembers.map((tm:TeamMemberInfo)=> {
            return (<Grid item xs={12} sm={6} md={4} alignContent="flex-end" > 
            <TeamMemberCard teamMember={tm}></TeamMemberCard>
            </Grid>);
          })}
            
              
            
          </Grid>
        </Container>

            
        </div>)
    }
}


export default withStyles(styles)(TeamPage);