import React, { Component } from "react";
import { Paper, Typography } from "@material-ui/core";

import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import { OurServicesDetails, ServiceDetails } from "../../data/BusinessDetails";
import CompanyDetailStore from "../../stores/BusinessDetailsStore";
import { inject, observer } from "mobx-react";
import ServicesSection from "../../component/services-section/ServicesSection";
import animateScrollTo, { IOptions } from 'animated-scroll-to';

const idPrefix:string = "my-element";
const styles = (theme: Theme) => ({
    root: {
      margin: theme.spacing(3, 2),
      padding: theme.spacing(3, 2),
    },
    mainHeading:{
      
    },
    card: {
      margin: theme.spacing(2, 2, 2, 2),
      maxWidth: 275,
    },
  });
  


export interface ServicesViewProps{
  classes?:any,
  companyDetailStore?: CompanyDetailStore,
  match?:any,

}


@inject('companyDetailStore')
@observer
class Services extends React.Component<ServicesViewProps> {
  
  componentDidMount(){

    console.log("match : "+this.props.match)
    if(this.props.match && this.props.match!.params && this.props.match.params.id){
      const index = this.props.match!.params.id;
      const pos = document.querySelector('#'+idPrefix+index);
      if(pos){
        const op :IOptions = {speed:2000};
        animateScrollTo(pos, op);
      }
      
    }

    
  }

    render(){
        const { classes } = this.props;
        
        return(<div>
          { 
          
          this.props.companyDetailStore!.businessDetails.ourServices!.services.map((serviceDetails:ServiceDetails, index:number)=>{

            return (<div id={idPrefix+(index+1)}><ServicesSection serviceDetails={serviceDetails}></ServicesSection></div>)
          })
          }
          <div ></div>
          </div>);
    }
    
}



export default withStyles(styles)(Services);