import React from "react";
import AboutUs, { AboutUsProps } from "../../AboutUs/AboutUs";
import OurServices, { OurServicesProps } from "../../OurServices/OurServices";
import HeroImageComponent  from "../../component/hero-image-component/HeroImageComponent";
import { HeroDetails } from "../../stores/HeroStore";
import { inject, observer } from "mobx-react";
import CompanyDetailStore from "../../stores/BusinessDetailsStore";
import { HeroImage } from "../../data/BusinessDetails";

interface HomePageViewProps {
    companyDetailStore?: CompanyDetailStore
}

@inject('companyDetailStore')
@observer
export class HomePageView extends React.Component<HomePageViewProps> {
    render() {
        const heroDetails:HeroImage = this.props.companyDetailStore!.businessDetails.homePage!.heroImage;

        const aboutUsProps:AboutUsProps = {
            displayLabel : "About Us",
            aboutUs : this.props.companyDetailStore!.businessDetails.aboutUs
        }

        const ourServiceProps : OurServicesProps ={
            ourServicesDetails : this.props.companyDetailStore!.businessDetails!.ourServices
        }

        return (
            <div>
                <HeroImageComponent {...heroDetails}> </HeroImageComponent>
                    <AboutUs {...aboutUsProps}></AboutUs>
                    <OurServices {...ourServiceProps}></OurServices>
        </div>)
            }
}