import React, { Component } from "react";
import { Container, Typography, Grid, Button, Zoom, Grow } from "@material-ui/core";

import './HeroImageComponent.css';
import * as _ from 'lodash';
import { HeroImage } from "../../data/BusinessDetails";
import { Link1 } from "./RoutingLinkButton";
import { CSSProperties } from "@material-ui/core/styles/withStyles";

export default class HeroImageComponent extends Component<HeroImage,any>{

    constructor(props:any){
        super(props);
        this.state = {motoAnimation :false,
            actionAnimation:false};
    }

    triggerMotoAnimation = () =>  {
        this.setState({motoAnimation: true})

    }
    triggerActionAnimation = () =>  {
        this.setState({actionAnimation: true})

    }


    renderPrimaryAction() {

        debugger;
        if (!_.isEmpty(this.props.primaryAction) &&
            !_.isEmpty(this.props.primaryAction!.displayLabel) &&
                !_.isEmpty(this.props.primaryAction!.url)) {
            return (
                <Grid item>

                    <Button variant="contained" color="primary" to={this.props.primaryAction!.url} component={Link1}>
                        {this.props.primaryAction!.displayLabel}
                    </Button>
                </Grid>
            )
        }
    }

    renderSecondaryAction() {
        if (!_.isEmpty(this.props.secondaryAction) &&
            !_.isEmpty(this.props.secondaryAction!.displayLabel) &&
                !_.isEmpty(this.props.secondaryAction!.url)) {
            return (
                <Grid item>
                    <Button variant="outlined" color="primary" to={this.props.secondaryAction!.url} component={Link1}>
                        {this.props.secondaryAction!.displayLabel}
                    </Button>
                </Grid>
            )
        }
    }

    
    render() {

        const imgUrl = this.props.imgUrl ? this.props.imgUrl : "https://image.shutterstock.com/z/stock-photo-businessman-hands-holding-pen-for-working-in-stacks-of-paper-files-searching-information-business-1116167936.jpg";

        

        const heroImageCss : CSSProperties= {
            marginTop : "1px",
        
            backgroundImage:"linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5 )), url('"+imgUrl+"')",
        
          /* Set a specific height */
          height: "100%",
        
          /* Position and center the image to scale nicely on all screens */
          backgroundPosition:"center",
          backgroundRepeat :"no-repeat",
          backgroundSize:"cover",
          position:"relative"
           
        }

        return (<div style={heroImageCss}>

            <Container maxWidth="lg">
            <Grow in={true} timeout={2700} onEntering={()=>{_.delay(this.triggerMotoAnimation,500)}}>
                <Typography variant="h2" align="center" color="textPrimary" gutterBottom>
                    {this.props.tagLine!}
                </Typography>
                </Grow>
                <Grow in={this.state.motoAnimation} timeout={2700} onEntering={()=>{_.delay(this.triggerActionAnimation,500)}}>
                <Typography variant="h5" align="center" color="textSecondary" paragraph>
                    <div className="highlighted" >{this.props.moto!}</div>
                </Typography>
                </Grow>
                
                <Grow in={this.state.actionAnimation} timeout={2500}>
                <div >
                    <Grid container spacing={2} justify="center" style={{height:'340'}} >
                        {this.renderPrimaryAction()}
                        {this.renderSecondaryAction()}
                    </Grid>
                </div>
                </Grow>
            </Container> 

        </div>)
    }
}