
import {observable} from 'mobx';
import * as _ from 'lodash';


var heroImageData = require('./../data/heroImage.json'); 

export class HeroDetails{
    @observable tagLine?:string;
    @observable moto?:string;
    @observable primaryAction?:string;
    @observable primaryActionUrl?:string;
    @observable secondaryAction?:string;
    @observable secondaryActionUrl?:string;

    constructor(){

        _.assign(this, heroImageData)
    }

}
