import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CallIcon from '@material-ui/icons/Call';
import PersonIcon from '@material-ui/icons/Person';
import { Paper, Typography, List, ListItem, ListItemIcon, ListItemText, Divider } from '@material-ui/core';

import RoomIcon from '@material-ui/icons/Room';

import { inject, observer } from "mobx-react";
import CompanyDetailStore from "../../stores/BusinessDetailsStore";

import './ContactUs.css'
import { Link, Grid } from "@material-ui/core";
import HeroImageComponent from "../../component/hero-image-component/HeroImageComponent";
import { OfficeDetail, OfficeMember, PhoneNumber } from "../../data/BusinessDetails";
import React from 'react';


interface ContactUsPageProps {
  companyDetailStore?: CompanyDetailStore
}

@inject('companyDetailStore')
@observer
export class ContactUsView extends React.Component<ContactUsPageProps> {
  renderHero() {

    return (<HeroImageComponent {...this.props.companyDetailStore?.businessDetails.contactPageInfo!.heroImage} ></HeroImageComponent>)
  }

  renderListItemOfficeDetails = () => {

    const items: Array<any> = [];

    const office = this.props.companyDetailStore?.businessDetails.contactPageInfo?.officeDetails?.map((ofd: OfficeDetail) => {
      return (<div>

          <Typography variant="button" color="textPrimary" component="h5" align="center" >
          {ofd.officeName}
        </Typography>
        <List component="nav">
          

          {ofd.members.map((mem: OfficeMember) => {
            return (<div>

          <ListItem button>
                <ListItemIcon>
                  <PersonIcon></PersonIcon>
                </ListItemIcon>
                <ListItemText primary={mem.name} />
                <Divider />
              </ListItem>

              {mem.phoneNumbers.map((ph: PhoneNumber) => {
                const telUrl = "tel:"+ph.dialNumber;
                const link = <Link color="primary" href={telUrl} >

                {ph.displayNumber}
              </Link>;
                return (<div>


          <ListItem button>
                    <ListItemIcon>
                      <CallIcon></CallIcon>
                    </ListItemIcon>
                    <ListItemText primary={link} />
                    <Divider />
                  </ListItem>
                </div>)
              })}

            </div>)
          })}
<ListItem button>
            <ListItemIcon>
              <RoomIcon></RoomIcon>
            </ListItemIcon>
            <ListItemText primary={ofd.address} />
            <Divider />
          </ListItem>
        </List>
        <Divider />


      </div>);
    })
    items.push(office);

    return items;
  }

  render() {
    return (
      <div>
        {this.renderHero()}
        <Grid container justify="center">
          <Grid item xs={12} md={6} >
            <Card className="contact-card">
              <CardContent>
                <Typography gutterBottom variant="h5" component="h2" align="center">
                  <CallIcon fontSize="large" color="primary" ></CallIcon><br />
                  Contact
      </Typography>


              </CardContent>

              <CardContent>

                {this.renderListItemOfficeDetails()}
              </CardContent>
            </Card>
          </Grid>
        </Grid>

      </div>)
  }
}