import React, { Component } from "react";
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';


import { withStyles } from "@material-ui/core/styles";
import './AppNavBar.css';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import { Link as RouterLink1, LinkProps as RouterLinkProps } from 'react-router-dom';
import { ContactUsView } from "../views/contact-us/ContactUs";
import { HomePageView } from "../views/home-page/HomePageView";
import Services from "../views/services/Services";
import TeamPage from "../views/team/TeamPage";


const pre=""
export interface RouteInfo{
  displayLabel:string;
  path:string;
  component:any
}
const routes:Array<RouteInfo> =[{
  displayLabel:"Home",
  path:pre+"/home",
  component : HomePageView
},
{
  displayLabel:"",
  path:pre+"/services/:id",
  component : Services
},{
  displayLabel:"Services",
  path:pre+"/services/",
  component : Services
},
{
  displayLabel:"Team",
  path:pre+"/team",
  component : TeamPage
},
{
  displayLabel:"Contact",
  path:pre+"/contact",
  component : ContactUsView
},
{
  displayLabel:"",
  path:"/",
  component : HomePageView
}]


const Link1 = React.forwardRef<HTMLAnchorElement, RouterLinkProps>((props, ref) => (
  <RouterLink1 innerRef={ref} {...props} />
));

export default class AppNavBar extends Component<any>{
  render() {

    return (<div>

    <AppBar position="static">
      <Toolbar variant="dense">

        <Typography variant="h5" className="nav-company-name">
          NTPL
      </Typography>


     

      {routes.filter((route:RouteInfo) => route.displayLabel != "") .map((route:RouteInfo)=>{
      return (

        
        <Button color="inherit" component={Link1} to={route.path}>
        {route.displayLabel}
      </Button>

        
        );
        })}
      </Toolbar>
    </AppBar>
      
      <Switch>
        {routes.map((route:RouteInfo)=>{
          return (<Route exact path={route.path} component={route.component}></Route>);
        })}
        
      </Switch>
      
      </div>

        )
      }
      }
